import React, { useState } from "react";
import VMS_URL from "../config/config";
import { useNavigate } from "react-router-dom";


export function LoginComponent() {

  const [theme, setTheme] = useState("light");

  const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState(null);
    const navigate = useNavigate();
    const handleSubmit = async (event) => {
      event.preventDefault();
      setError(null);
  
      try {
          console.log('1');
          
          const url = `${VMS_URL}SignIn?Email=${encodeURIComponent(email)}&Password=${encodeURIComponent(password)}`;
          
          const response = await fetch(url, {
              method: 'GET',
              headers: {
                  'Content-Type': 'application/json',
              },
          });
          
          console.log(url);  // Check the URL being sent
          
          const result = await response.json();
          console.log(result);
          
          if (response.ok) {
              console.log('2', result);
              if (result.Status) {
                  console.log('Sign-in successful', result);
                  sessionStorage.setItem("userData", JSON.stringify(result["ResultData"][0]));
                  navigate('/visiting');
              }
          } else {
              console.error('Error during sign-in', result.error);
              setError(result.error);
          }
      } catch (err) {
          setError('Something went wrong. Please try again later.');
      }
  };
  

  

  // Dynamically set background image based on the theme
  const backgroundStyle = {
    backgroundImage:
      theme === "dark"
        ? "url('assets/media/auth/bg10-dark.jpeg')"
        : "url('assets/media/auth/bg10.jpeg')",
    minHeight: "100vh",
    backgroundSize: "cover",
    backgroundRepeat: "no-repeat",
    backgroundPosition: "center",
  };

  return (
    <div className="d-flex flex-column flex-root" id="kt_app_root" style={backgroundStyle}>
      <div className="d-flex flex-column flex-lg-row flex-column-fluid">
        {/* Left Section */}
        <div className="d-flex flex-lg-row-fluid">
          <div className="d-flex flex-column flex-center pb-0 pb-lg-10 p-10 w-100">

            <img
              className="theme-dark-show mx-auto mw-100 w-150px w-lg-300px mb-10 mb-lg-20"
              src="assets/media/auth/agency-dark.png"
              alt="Dark Theme Logo"
            />
            <h1 className="text-gray-800 fs-2qx fw-bold text-center mb-7">
              Fast, Efficient, and Productive
            </h1>
            <div className="text-gray-600 fs-base text-center fw-semibold">
              In this kind of post,
              <a href="#" className="opacity-75-hover text-primary me-1">
                the blogger
              </a>
              introduces a person theyve interviewed
              <br />
              and provides some background information about
              <a href="#" className="opacity-75-hover text-primary me-1">
                the interviewee
              </a>
              and their
              <br />
              work following this is a transcript of the interview.
            </div>
          </div>
        </div>

        {/* Right Section */}
        <div className="d-flex flex-column-fluid flex-lg-row-auto justify-content-center justify-content-lg-end p-12">
          <div className="bg-body d-flex flex-column flex-center rounded-4 w-md-600px p-10">
            <div className="d-flex flex-center flex-column align-items-stretch h-lg-100 w-md-400px">
              {/* Form */}
              <div className="d-flex flex-center flex-column flex-column-fluid pb-15 pb-lg-20">
                <form className="form w-100" id="kt_sign_in_form" action="#" onSubmit={handleSubmit}>
                  <div className="text-center mb-11">
                    <h1 className="text-dark fw-bolder mb-3">Sign In</h1>
                    <div className="text-gray-500 fw-semibold fs-6">Your Social Campaigns</div>
                  </div>

                  <div className="row g-3 mb-9">
                    <div className="col-md-6">
                      <a
                        href="#"
                        className="btn btn-flex btn-outline btn-text-gray-700 btn-active-color-primary bg-state-light flex-center text-nowrap w-100"
                      >
                        <img
                          alt="Logo"
                          src="assets/media/svg/brand-logos/google-icon.svg"
                          className="h-15px me-3"
                        />
                        Sign in with Google
                      </a>
                    </div>
                    <div className="col-md-6">
                      <a
                        href="#"
                        className="btn btn-flex btn-outline btn-text-gray-700 btn-active-color-primary bg-state-light flex-center text-nowrap w-100"
                      >
                        <img
                          alt="Logo"
                          src="assets/media/svg/brand-logos/apple-black.svg"
                          className="theme-light-show h-15px me-3"
                        />
                        <img
                          alt="Logo"
                          src="assets/media/svg/brand-logos/apple-black-dark.svg"
                          className="theme-dark-show h-15px me-3"
                        />
                        Sign in with Apple
                      </a>
                    </div>
                  </div>

                  <div className="separator separator-content my-14">
                    <span className="w-125px text-gray-500 fw-semibold fs-7">Or with email</span>
                  </div>

                  <div className="fv-row mb-8">
                                <input
                                    type="text"
                                    placeholder="Email"
                                    name="email"
                                    value={email}
                                    onChange={(e) => setEmail(e.target.value)}
                                    className="form-control bg-transparent"
                                />
                            </div>

                            {/* Password Field */}
                            <div className="fv-row mb-3">
                                <input
                                    type="password"
                                    placeholder="Password"
                                    name="password"
                                    value={password}
                                    onChange={(e) => setPassword(e.target.value)}
                                    className="form-control bg-transparent"
                                />
                            </div>

                            {/* Error Message */}
                            {error && <div className="text-danger mb-3">{error}</div>}

                            {/* Submit Button */}
                            <div className="d-grid mb-10">
                                <button type="submit" className="btn btn-primary">
                                    <span className="indicator-label">Sign In</span>
                                </button>
                            </div>
                </form>
              </div>

              {/* Footer Links */}
              <div className="d-flex flex-stack">
                <div className="me-10">
                  <button className="btn btn-link btn-color-gray-700">
                    <img
                      className="w-20px h-20px rounded me-3"
                      src="assets/media/flags/united-states.svg"
                      alt="English"
                    />
                    <span>English</span>
                  </button>
                </div>
                <div className="d-flex fw-semibold text-primary fs-base gap-5">
                  <a href="../../demo1/dist/pages/team.html" target="_blank">
                    Terms
                  </a>
                  <a href="../../demo1/dist/pages/pricing/column.html" target="_blank">
                    Plans
                  </a>
                  <a href="../../demo1/dist/pages/contact.html" target="_blank">
                    Contact Us
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
