
import React, { useState, useEffect, useRef } from "react";
import Base from "../config/Base";
import { Popover } from 'antd';
import VMS_URL from '../config/config.jsx';
import Swal from 'sweetalert2';
import axios from 'axios';
import PrintPassContent from './print';
import QRCode from 'qrcode'; 

export default function VisitorComponent() {

    const [sessionUserData, setsessionUserData] = useState([]);
    const [visitorsData, setVistorsData] = useState([]);
    const [dataLoading, setDataLoading] = useState(false);
    const [addSubmitLoading, setAddSubmitLoading] = useState(false);
    const [attendees, setAttendees] = useState([]);
    const printRef = useRef();
    const [qrCodeUrl, setQrCodeUrl] = useState(null);
    const [printData, setPrintData] = useState([]);
    const [editFormData, setEditFormData] = useState({
        orgid: sessionUserData.OrgId,
            userid: '',
            id: '',
            VisitorName: '',
            PhoneNumber: '',
            Email: '',
            VehicleInfo: '',
            NoOfMembers: '',
            ReturnableMaterial: '',
            Remarks: '',
            Date: '',
            MeetingDate: '',
            Status: '',
            UpdatedBy: sessionUserData.Id,
            Attendees: []
    });

    useEffect(() => {
        const userDataString = sessionStorage.getItem('userData');
        if (userDataString) {
            const userData = JSON.parse(userDataString);
            setsessionUserData(userData);
        } else {
            console.log('User data not found in sessionStorage');
        }
    }, []);

    const [formData, setFormData] = useState({
        orgid: sessionUserData.OrgId,
        userid: sessionUserData.RegId,
        Operation: "ADD",
        RequestPass: {
          VisitorName: "killer",
          Email: "jshsjj@gmail.com",
          Mobile: "98765433456",
          CompanyName: "fjiud",
          RequestDate: "",
          MeetingDate: "",
          IsVehicle: false,
          VehicleInfo: "",
          ReturnOfMaterials: "",
          Remarks: "",
          NoOfMembers: 0,
          VisitorType: "1",
          Status: "DRAFT",
          IsActive: 1,
        },
        Attendees: [
        ],
      });

    const fetchData = async () => {
        setDataLoading(true);
        try {
            const response = await fetch(`${VMS_URL}getReqPass?OrgId=${sessionUserData.OrgId}`);
            if (response.ok) {
                const data = await response.json();
                setVistorsData(data.ResultData);
            } else {
                console.error('Failed to fetch attendance data:', response.statusText);
            }
        } catch (error) {
            console.error('Error fetching attendance data:', error.message);
        } finally {
            setDataLoading(false);
        }
    };

    useEffect(() => {
        if (sessionUserData.OrgId) {
            fetchData();
        }
    }, [sessionUserData]);

    const handleRequestPassChange = (e) => {
        const { name, value } = e.target;
        setFormData((prevState) => ({
            ...prevState,
            RequestPass: {
                ...prevState.RequestPass,
                [name]: value,
            },
        }));
    };
    

    //   const handleAttendeeChange = (index, key, value) => {
    //     const updatedAttendees = [...formData.Attendees];
    //     updatedAttendees[index][key] = value;
    //     setFormData((prevState) => ({
    //       ...prevState,
    //       Attendees: updatedAttendees,
    //     }));
    //   };
      
    
    //   const addAttendee = () => {
    //     setFormData((prevState) => ({
    //       ...prevState,
    //       Attendees: [
    //         ...prevState.Attendees,
    //         {
    //           AttendeName: "",
    //           PhoneNumber: "",
    //           Email: "",
    //           Remarks: "",
    //         },
    //       ],
    //     }));
    //   };
      
     
    //   const removeAttendee = (index) => {
    //     const updatedAttendees = formData.Attendees.filter((_, i) => i !== index);
    //     setFormData((prevState) => ({
    //       ...prevState,
    //       Attendees: updatedAttendees,
    //     }));
    //   };

    // const handleChange = (e) => {
    //     const { name, value } = e.target;
    //     setFormData({ ...formData, [name]: value });
    // };

    const addAttendee = () => {
        setAttendees([...attendees, { name: "", number: "", email: "" }]);
    };
    
    const removeAttendee = (index) => {
        setAttendees(attendees.filter((_, i) => i !== index));
    };

    
    const handleAttendeeChange = (index, field, value) => {
        const updatedAttendees = [...attendees];
        updatedAttendees[index][field] = value;
        setAttendees(updatedAttendees);
    };

    const formatDateInput = (date) => {
        const inputDate = typeof date === 'string' ? new Date(date) : date;
        
        const pad = (n) => n.toString().padStart(2, '0');
        const year = inputDate.getFullYear();
        const month = pad(inputDate.getMonth() + 1);
        const day = pad(inputDate.getDate());
        const hours = pad(inputDate.getHours());
        const minutes = pad(inputDate.getMinutes());
        
        return `${year}-${month}-${day} ${hours}:${minutes}`;
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        console.log(sessionUserData)
        try {
            setAddSubmitLoading(true);
            formData.userid = sessionUserData.Id;
            formData.orgid = sessionUserData.OrgId;
            formData.CreatedBy = sessionUserData.Id;
            // formData.Date = formatDateInput(formData.Date);

            const updatedFormData = { ...formData, attendees: attendees };
            // console.log(updatedFormData, 'data sending to api');

            const response = await fetch(`${VMS_URL}ManageRequestPass`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(updatedFormData),
            });
            if (response.ok) {
                const data = await response.json();
                // console.log('Request data', data);
                setAddSubmitLoading(false);
    
                if (data[0].Success == 1) {
                    Swal.fire({
                        title: 'Success',
                        text: 'The record has been added successfully.',
                        icon: 'success',
                    }).then(() => {
                        window.location.reload();
                    });
                } else {
                    Swal.fire({
                        title: 'Error',
                        text: 'There was an error adding the record.',
                        icon: 'error',
                    });
                    setAddSubmitLoading(false);
                }
            } else {
                console.error('Request failed', response);
                Swal.fire({
                    title: 'Error',
                    text: 'Failed to submit request',
                    icon: 'error',
                });
                setAddSubmitLoading(false);
            }
        } catch (error) {
            console.error('Error:', error);
            Swal.fire({
                title: 'Error',
                text: 'An error occurred',
                icon: 'error',
            });
            setAddSubmitLoading(false);
        }
    };

    const [viewDataId, setViewDataId] = useState([]);
    const [editData, setEditData] = useState([]);
    const [viewLoading, setViewLoading] = useState(false);
    const [viewData, setViewData] = useState([]);
    const [searchQuery, setSearchQuery] = useState("");

    const formatDateForInput = (dateString) => {
        return dateString ? dateString.split(' ')[0] : '';
    };

    const filteredData = visitorsData.filter((item) => {
        const visitorName = item?.VisitorName?.toLowerCase() || '';
        const status = item?.Status?.toLowerCase() || '';
        const autoIncId = item?.AutoIncId?.toString() || '';

        return (
            visitorName.includes(searchQuery.toLowerCase()) ||
            status.includes(searchQuery.toLowerCase()) ||
            autoIncId.includes(searchQuery)
        );
    });

    const fetchViewEditData = async () => {
        setViewLoading(true);
        console.log(viewDataId)
        try {
            const response = await fetch(`${VMS_URL}getReqPassById?RequestId=${viewDataId}`);
            if (response.ok) {
                const data = await response.json();
                if (data.Status) {
                    setViewData(data.ResultData);
                    setEditData(data.ResultData);
                    // console.log(data, 'data of view and edit')
                    console.log(editData)
                } else {
                    setViewData([]);
                    setEditData([]);
                }
            } else {
                console.error('Failed to fetch attendees data:', response.statusText);
            }
        } catch (error) {
            console.error('Error fetching attendees data:', error.message);
        } finally {
            setViewLoading(false);
        }
    };
    useEffect(() => {
        fetchViewEditData();
    }, [viewDataId]);
    

    useEffect(() => {
        setEditFormData({
            orgid: sessionUserData.OrgId,
            userid: 1,
            id: editData[0]?.RequestId || '',
            VisitorName: editData[0]?.VisitorName || '',
            PhoneNumber: editData[0]?.Mobile || '',
            Email: editData[0]?.Email || '',
            VehicleInfo: editData[0]?.VehicleInfo || '',
            NoOfMembers: editData[0]?.NoOfMembers || '',
            ReturnableMaterial: editData[0]?.ReturnOfMaterials || '',
            Remarks: editData[0]?.Remarks || '',
            Date: editData[0]?.RequestDate || '',
            MeetingDate: editData[0]?.MeetingDate || '',
            Status: editData[0]?.Status || '',
            UpdatedBy: sessionUserData.Id,
            Attendees: editData?.Attendees || []
        });
    }, [editData]);

    const handleEditChange = (e) => {
        const { name, value } = e.target;
        setEditFormData((prevData) => ({
            ...prevData,
            [name]: value
        }));
    };

    const handleEditAddAttendee = () => {
        setEditFormData((prevData) => ({
            ...prevData,
            Attendees: [
                ...prevData.Attendees,
                { AttendeName: '', PhoneNumber: '', Email: '', Remarks: '', detailsid: null }
            ]
        }));
    };

    const handleEditAttendeeChange = (index, field, value) => {
        const updatedAttendees = editFormData?.Attendees.map((attendee, idx) =>
            idx === index ? { ...attendee, [field]: value } : attendee
        );
        setEditFormData((prevData) => ({ ...prevData, Attendees: updatedAttendees }));
    };

    const handleEditRemoveAttendee = (index) => {
        const updatedAttendees = editFormData?.Attendees.filter((_, idx) => idx !== index);
        setEditFormData((prevData) => ({ ...prevData, Attendees: updatedAttendees }));
    };

    // console.log(viewData)
    const handleSave = async () => {
        try {
            const response = await axios.post(`${VMS_URL}managerequestpass`, {
                orgid: editFormData?.orgid,
                id: editFormData?.id,
                userid: editFormData?.userid,
                Operation: "ADD", // or "EDIT" if applicable
                RequestPass: {
                    VisitorName: editFormData?.VisitorName,
                    Email: editFormData?.Email,
                    Mobile: editFormData?.PhoneNumber,
                    CompanyName: "HERO Corp", // Add this dynamically if needed
                    RequestDate: editFormData?.Date,
                    MeetingDate: editFormData?.MeetingDate,
                    IsVehicle: editFormData?.VehicleInfo ? true : false,
                    VehicleInfo: editFormData?.VehicleInfo,
                    ReturnOfMaterials: editFormData?.ReturnableMaterial,
                    Remarks: editFormData?.Remarks,
                    NoOfMembers: editFormData?.NoOfMembers,
                    VisitorType: "1",
                    Status: editFormData?.Status,
                    IsActive: 1
                },
                Attendees: editFormData?.Attendees
            });
            console.log('Response:', response.data);
        } catch (error) {
            console.error('Error saving data:', error);
        }
    };

    const handleEditSubmit = async (e) => {
        e.preventDefault();
        editFormData.Date = formatAcceptDate(editFormData.Date);
        console.log(editFormData, 'data sending to api')
        const response = await fetch(`${VMS_URL}ManageRequestPass`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(editFormData)
        });

        if (response.ok) {
            const data = await response.json();
            console.log(data)
            if (data.Status) {
                alert('Record updated successfully!');
            } else {
                alert('Error updating record');
            }
        } else {
            alert('Error updating record');
        }
    };

    const formatAcceptDate = (dateString) => {
        return dateString.split(" ")[0];
    };


    const formatDate = (dateString) => {
        const date = new Date(dateString);
        const formattedDate = date.toLocaleDateString('en-GB');
        const formattedTime = date.toLocaleTimeString('en-GB', {
            hour: '2-digit',
            minute: '2-digit',
        });
        return `${formattedDate} ${formattedTime}`;
    };

    const handleView = (item) => {
        setViewDataId(item.RequestId);
    };
    const handleEditBtn = (item) => {
        setViewDataId(item.RequestId);
    };

    const handlePrintVisit = async (item) => {
        setPrintData(item);
    
        const qrData = `
          Visitor Name: ${item.VisitorName}
          Phone Number: ${item.PhoneNumber}
          Vehicle Number: ${item.VehicleInfo}
        `;
    
        try {
          const qrCodeDataUrl = await QRCode.toDataURL(qrData);
          setQrCodeUrl(qrCodeDataUrl);
    
          if (printRef.current) {
            const printWindow = window.open('', '_blank');
            
            printWindow.document.write(`
                <html>
                    <head>
                        <title>Print</title>
                        <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/bootstrap/5.3.0/css/bootstrap.min.css" />
                        <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.7.0/css/all.min.css" />
                        <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.7.1/css/all.min.css" integrity="sha512-5Hs3dF2AEPkpNAR7UiOHba+lRSJNeM2ECkwxUIxC1Q/FLycGTbNapWXB4tP889k5T5Ju8fs4b1P5z/iB4nMfSQ==" crossorigin="anonymous" referrerpolicy="no-referrer" />
                    </head>
                    <body>
                        <div>${printRef.current.innerHTML}</div>
                    </body>
                </html>
            `);
            
            printWindow.document.close();
    
            printWindow.onload = () => {
                printWindow.focus();
                printWindow.print();
                printWindow.close();
            };
            } else {
                console.error('printRef.current is undefined');
            }
        } catch (error) {
            console.error('Error generating QR code:', error);
        }
    };

    const [acceptFormData, setAcceptFormData] = useState(null);

    const handleAction = (item, action) => {
        const actionText = action === 'accept' ? 'accept' : 'reject';
        const confirmButtonText = action === 'accept' ? 'Yes, accept it!' : 'Yes, reject it!';
    
        Swal.fire({
            title: `Are you sure?`,
            text: `Do you want to ${actionText} this item?`,
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: confirmButtonText
        }).then((result) => {
            if (result.isConfirmed) {
                setAcceptFormData(item);
                handleActionSubmit(item, action);
            }
        });
    };
    
    const handleActionSubmit = async (item, action) => {
        const statusText = action === 'accept' ? 'APPROVE' : 'REJECT';
        
        const payload = {
            "RequestId": item.RequestId,
            "Status": statusText,
            "UserId": sessionUserData.Id,
            "to": item.Email,
            "subject": `Your Pass Request ${statusText}`,
            "text": `
                Dear ${item.VisitorName},
        
                We would like to inform you that your pass request (Request ID: ${item.RequestId}) has been ${statusText.toLowerCase()}.
        
                ${statusText === 'APPROVE' ? 
                    'Congratulations! Your pass request has been approved. You can now visit the premises as per the guidelines provided.' : 
                    'Unfortunately, your pass request has been rejected. Please feel free to reach out if you have any questions or require further assistance.'
                }
        
                For more details, you can visit your request page using the link below:
                http://vmsdemo1.s3-website.eu-north-1.amazonaws.com/${item.RequestId}
        
                Best regards,
                Visitor Management System (VMS)
            `,
            "html": `
                <h1>${statusText === 'APPROVE' ? 'Congratulations!' : 'Notice'}</h1>
                <p>Dear ${item.VisitorName},</p>
                <p>We would like to inform you that your pass request (Request ID: ${item.AutoIncNo}) has been ${statusText.toLowerCase()}.</p>
                <p>
                    ${statusText === 'APPROVE' ? 
                        'Congratulations! Your pass request has been approved. You can now visit the premises as per the guidelines provided.' : 
                        'Unfortunately, your pass request has been rejected. Please feel free to reach out if you have any questions or require further assistance.'
                    }
                </p>
                <p>
                    For more details, you can visit your request page using the link below:
                    <br>
                    <a href="http://vmsdemo1.s3-website.eu-north-1.amazonaws.com/${item.RequestId}">View Your Request</a>
                </p>
                <p>Best regards,<br>Copper Wind Industry</p>
            `
        };
        
        console.log(payload, 'payloadpayload')
    
        try {
            const response = await fetch(`${VMS_URL}PassApproval&Email`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(payload)
            });

            const result = await response.json();
            console.log(result)
            
            if (response.ok) {
                if (result.Status) {
                    fetchData();
                }
                throw new Error("Request failed with status " + response.status);
            }
            // Handle success response if needed
            console.log("Request successful:", await response.json());
        } catch (error) {
            console.error("Error submitting action:", error);
        }
    };

// console.log(editFormData.VisitorName)
      

    return (
        <Base>
        <div className="d-flex">
            <input 
                type="text"
                className="form-control"
                placeholder="Search..."
                style={{ width: '20rem' }}
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
            />
            <button 
                className="btn btn-primary d-flex ms-auto" 
                type="button" 
                data-bs-toggle="offcanvas" 
                data-bs-target="#offcanvasRight" 
                aria-controls="offcanvasRight"
            >
                Create Meet
            </button>
        </div>
            <div className="table-responsive">
                <table className="table align-middle table-row-dashed fs-6 gy-5 dataTable" id="kt_ecommerce_sales_table">
                    
                    <thead>
                        <tr className="text-start text-gray-500 fw-bold fs-7 text-uppercase gs-0">
                            <th className="text-start dt-orderable-none" >S.No</th>
                            <th className="dt-orderable-asc dt-orderable-desc" ><span className="dt-column-title"
                                    role="button">ID</span><span className="dt-column-order"></span></th>
                            <th className="dt-orderable-asc dt-orderable-desc" ><span className="dt-column-title"
                                    role="button">Visitor</span><span className="dt-column-order"></span></th>
                           
                            <th className=" dt-type-numeric dt-orderable-asc dt-orderable-desc"><span
                                    className="dt-column-title" role="button">Date</span><span className="dt-column-order"></span></th>
                            <th className=" dt-type-date dt-orderable-asc dt-orderable-desc" ><span
                                    className="dt-column-title" role="button">Phone</span><span className="dt-column-order"></span>
                            </th>
                            <th className=" dt-type-date dt-orderable-asc dt-orderable-desc" ><span
                                    className="dt-column-title" role="button">No Of Members</span><span className="dt-column-order"></span>
                            </th>
                            <th className=" dt-orderable-asc dt-orderable-desc" ><span className="dt-column-title"
                                    role="button">Status</span><span className="dt-column-order"></span></th>
                            <th className=" dt-orderable-none" ><span className="dt-column-title">Actions</span><span
                                    className="dt-column-order"></span></th>
                        </tr>
                    </thead>
                    <tbody className="fw-semibold text-gray-600">
                        {filteredData && filteredData?.map((item, index) => (
                            <tr key={index}>
                                <td className="text-start">{index + 1}</td>
                                <td className="text-start dt-type-numeric">
                                    {item.AutoIncNo}
                                </td>
                                <td>
                                    <a className="text-gray-800 text-hover-primary fs-5 fw-bold">{item.VisitorName}</a>
                                </td>
                                <td>
                                    <div className="badge badge-light-danger">{formatDate(item.RequestDate)}</div>
                                </td>
                                <td>
                                    <span className="fw-bold">{item.Mobile}</span>
                                </td>
                                <td >
                                    <span className="fw-bold">{item.NoOfMembers}</span>
                                </td>
                            
                                <td>
                                    <div className={
                                        item.Status === "DRAFT" ? "badge badge-light-dark" :
                                        item.Status === "APPROVE" ? "badge badge-light-info" :
                                        item.Status === "REJECT" ? "badge badge-light-danger" :
                                        item.Status === "COMPLETED" ? "badge badge-light-success" :
                                        "badge badge-light"
                                    }>
                                        {item.Status}
                                    </div>
                                </td>

                                <td style={{ cursor: 'pointer' }}>
                                    <Popover 
                                        placement="bottom" 
                                        content={
                                            <div style={{ width: '8rem' }}>
                                                <p 
                                                    style={{ cursor: 'pointer' }}
                                                    className="text-hover-warning"
                                                    data-bs-toggle="offcanvas" 
                                                    data-bs-target="#offcanvasRightView" 
                                                    aria-controls="offcanvasRightView"    
                                                    onClick={() => handleView(item)}
                                                
                                                ><i className="fa-regular fa-eye me-2"></i>View</p>
                                                <p 
                                                    style={{ cursor: 'pointer' }} 
                                                    className="text-hover-warning"
                                                    data-bs-toggle="offcanvas" 
                                                    data-bs-target="#offcanvasRightEdit" 
                                                    aria-controls="offcanvasRightEdit"    
                                                    onClick={() => handleEditBtn(item)}
                                                >
                                                    <i className="fa-regular fa-pen-to-square me-2 text-info"></i>
                                                    Edit
                                                </p>
                                                <p
                                                    style={{
                                                        cursor: sessionUserData.RoleId === 1 && (item.Status == 'DRAFT' || item.Status == 'REJECT') ? 'pointer' : 'not-allowed',
                                                        pointerEvents: sessionUserData === 1 && (item.Status == 'DRAFT' || item.Status == 'REJECT') ? 'auto' : 'none',
                                                        opacity: sessionUserData === 1 && (item.Status === 'DRAFT' || item.Status === "REJECT") ? 1 : 0.5
                                                    }}
                                                    className="text-hover-warning"
                                                    onClick={() => (sessionUserData === 1 && !(item.Status === 1 || item.Status === 3)) ? handleAction(item, 'accept') : null}
                                                    // onClick={() =>handleAction(item, 'accept')}
                                                >
                                                    <i className="fa-solid fa-check me-2 text-success"></i>Accept
                                                </p>
                                                <p 
                                                    style={{
                                                        cursor: sessionUserData === 1 && (item.Status == 'DRAFT' || item.Status == 'APPROVE') ? 'pointer' : 'not-allowed',
                                                        pointerEvents: sessionUserData === 1 && (item.Status == 'DRAFT' || item.Status === 2) ? 'auto' : 'none',
                                                        opacity: sessionUserData === 1 && (item.Status == 'DRAFT' || item.Status == 'APPROVE') ? 1 : 0.5
                                                    }}
                                                    className="text-hover-warning"
                                                    onClick={() => (sessionUserData === 1 && (item.Status === 'DRAFT' || item.Status === 'APPROVE')) ? handleAction(item, 'reject') : null}
                                                    // onClick={() => handleAction(item, 'reject')}
                                                >
                                                    <i className="fa-solid fa-xmark me-2 text-danger"></i>Reject
                                                </p>
                                                <p 
                                                    style={{ cursor: 'pointer' }} 
                                                    className="text-hover-warning"
                                                    onClick={() =>handlePrintVisit(item)}
                                                >
                                                    <i className="fa-solid fa-print me-2"></i>Print
                                                </p>
                                            </div>
                                        }
                                        trigger="click"
                                    >
                                        <button 
                                            className="btn"
                                        >
                                            <i className="fa-solid fa-ellipsis-vertical"></i>
                                        </button>
                                    </Popover>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                    <tfoot></tfoot>
                </table>
            </div>

            {/* add */}
            <div className="offcanvas offcanvas-end" tabIndex="-1" id="offcanvasRight" aria-labelledby="offcanvasRightLabel"
                style={{ minWidth: '45%', maxWidth: '45%' }}
            >
                <form onSubmit={handleSubmit}>
                    <div className="offcanvas-header d-flex justify-content-between align-items-center">
                       
                        <h5 id="offcanvasRightLabel" className="mb-0">Create Visit</h5>
                        <div className="d-flex align-items-center">
                            <button 
                                className="btn btn-primary me-2"
                                type="submit"
                            >
                                {addSubmitLoading ? 'Submitting...' : 'Submit'}
                            </button>
                            <button 
                                type="button" 
                                className="btn-close" 
                                data-bs-dismiss="offcanvas" 
                                aria-label="Close"
                            ></button>
                        </div>
                    </div>
                    <div className="offcanvas-body" style={{ marginTop: '-2rem', maxHeight: '42rem', overflowY: 'auto' }}>
                        <div className="row">
                            <div className="col-6 mb-2">
                                <label className="form-label justify-content-start d-flex">Date<span className="text-danger fw-bold">*</span></label>
                                <input 
                                    type="date"
                                    name="MeetingDate"
                                    className="form-control"
                                    onChange={handleRequestPassChange}
                                    required
                                />
                            </div>
                            <div className="col-6 mb-2">
                                <label className="form-label justify-content-start d-flex">Attendee Name<span className="text-danger fw-bold">*</span></label>
                                <input 
                                    type="text"
                                    name="VisitorName"
                                    className="form-control"
                                    placeholder="Enter Attendee name"
                                    onChange={handleRequestPassChange}
                                    required
                                />
                            </div>
                            <div className="col-6 mb-2">
                                <label className="form-label justify-content-start d-flex">Mobile No<span className="text-danger fw-bold">*</span></label>
                                <input 
                                    type="number"
                                    name="Mobile"
                                    className="form-control"
                                    placeholder="Enter Mobile number"
                                    onWheel={(e) => e.target.blur()}
                                    onChange={handleRequestPassChange}
                                />
                            </div>
                            <div className="col-6 mb-2">
                                <label className="form-label justify-content-start d-flex">Email Id<span className="text-danger fw-bold">*</span></label>
                                <input 
                                    type="email"
                                    name="Email"
                                    className="form-control"
                                    placeholder="Enter Email address"
                                    onChange={handleRequestPassChange}
                                    required
                                />
                            </div>
                            <div className="col-12 mb-2">
                                <label className="form-label justify-content-start d-flex">Remarks</label>
                                <textarea 
                                    name="Remarks"
                                    className="form-control" 
                                    rows={3} 
                                    placeholder="Enter notes.."
                                    onChange={handleRequestPassChange}
                                ></textarea>
                            </div>
                            <div className="d-flex mt-2">
                                <h6 className="text-start mt-1">Attendee List:</h6>
                                <button className="btn btn-info d-flex ms-auto text-hover-primary" type="button" onClick={addAttendee}>
                                    <i className="fa-solid fa-person-circle-plus fs-3"></i>
                                </button>
                            </div>
                            {attendees.map((attendee, index) => (
                                <div key={index} className="row mb-2 align-items-center">
                                    <div className="col-4">
                                        <label className="form-label justify-content-start d-flex">
                                            Name <span className="text-danger fw-bold">*</span>
                                        </label>
                                        <input
                                            type="text"
                                            className="form-control"
                                            value={attendee.name}
                                            onChange={(e) => handleAttendeeChange(index, 'name', e.target.value)}
                                            placeholder="Attendee name"
                                        />
                                    </div>
                                    <div className="col-3">
                                        <label className="form-label justify-content-start d-flex">
                                            Mobile <span className="text-danger fw-bold">*</span>
                                        </label>
                                        <input
                                            type="number"
                                            className="form-control"
                                            value={attendee.number}
                                            onChange={(e) => handleAttendeeChange(index, 'number', e.target.value)}
                                            onWheel={(e) => e.target.blur()}
                                            placeholder="Mobile no"
                                        />
                                    </div>
                                    <div className="col-4">
                                        <label className="form-label justify-content-start d-flex">
                                            Email <span className="text-danger fw-bold">*</span>
                                        </label>
                                        <input
                                            type="email"
                                            className="form-control"
                                            value={attendee.email}
                                            onChange={(e) => handleAttendeeChange(index, 'email', e.target.value)}
                                            placeholder="Email id"
                                        />
                                    </div>
                                    <div className="col-1 d-flex justify-content-end btn text-hover-warning" style={{ marginTop: '2rem', cursor: 'pointer' }}>
                                        <i className="fa-regular fa-trash-can fs-4 text-danger" onClick={() => removeAttendee(index)}></i>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                </form>
            </div>

            {/* view */}
            <div className="offcanvas offcanvas-end" tabIndex="-1" id="offcanvasRightView" aria-labelledby="offcanvasRightLabel"
                style={{ minWidth: '52%', maxWidth: '52%' }}
            >
                <div className="offcanvas-header">
                    <h5 id="offcanvasRightLabel">View Request</h5>
                    <button type="button" className="btn-close text-reset" data-bs-dismiss="offcanvas" aria-label="Close"></button>
                </div>
                    <div className="offcanvas-body" style={{ marginTop: '-2rem' }}>
                        <div className="row">
                            <div className="col-6 mb-2">
                                <label className="form-label justify-content-start d-flex">Date</label>
                                <input 
                                    type="date"
                                    className="form-control"
                                    value={formatDateForInput(viewData[0]?.MeetingDate)}
                                    readOnly
                                />
                            </div>
                            <div className="col-6 mb-2">
                                <label className="form-label justify-content-start d-flex">Attendee Name</label>
                                <input 
                                    type="text"
                                    className="form-control"
                                    placeholder="Attendee name"
                                    value={viewData[0]?.VisitorName || "N/A"}
                                    readOnly
                                />
                            </div>
                            <div className="col-6 mb-2">
                                <label className="form-label justify-content-start d-flex">Mobile No</label>
                                <input 
                                    type="number"
                                    className="form-control"
                                    placeholder="Mobile number"
                                    value={viewData[0]?.Mobile}
                                    readOnly
                                />
                            </div>
                            <div className="col-6 mb-2">
                                <label className="form-label justify-content-start d-flex">Email Id</label>
                                <input 
                                    type="email"
                                    name="Email"
                                    className="form-control"
                                    placeholder="Enter Email address"
                                    value={viewData[0]?.Email}
                                    readOnly
                                />
                            </div>
                            <div className="col-6 mb-2">
                                <label className="form-label justify-content-start d-flex">Vehicle No</label>
                                <input 
                                    type="text"
                                    className="form-control"
                                    placeholder="Vehicle number"
                                    value={viewData[0]?.VehicleInfo}
                                    readOnly
                                />
                            </div>
                            <div className="col-12 mb-2">
                                <label className="form-label justify-content-start d-flex">Remarks</label>
                                <textarea 
                                    name="Remarks"
                                    className="form-control" 
                                    rows={3} 
                                    placeholder="Notes.."
                                    value={viewData[0]?.Remarks}
                                    readOnly
                                ></textarea>
                            </div>
                        </div>
                        <h4 className={`text-start my-2 ${viewData?.visitors?.length > 0 ? 'd-block' : 'd-none'}`}>Attndees:</h4>
                        {Array.isArray(viewData) && viewData.map((attendee, index) => (
                            <div key={index} className="row mb-2 align-items-center">
                                <div className="col-4">
                                    <label className="form-label justify-content-start d-flex">
                                        Name
                                    </label>
                                    <input
                                        type="text"
                                        className="form-control"
                                        value={attendee.AttendeName}
                                        placeholder="Attendee name"
                                        readOnly
                                    />
                                </div>
                                <div className="col-4">
                                    <label className="form-label justify-content-start d-flex">
                                        Mobile
                                    </label>
                                    <input
                                        type="number"
                                        className="form-control"
                                        value={attendee.PhoneNumber}
                                        placeholder="Mobile no"
                                        readOnly
                                    />
                                </div>
                                <div className="col-4">
                                    <label className="form-label justify-content-start d-flex">
                                        Email
                                    </label>
                                    <input
                                        type="email"
                                        className="form-control"
                                        value={attendee.AttendeEmail}
                                        placeholder="Email id"
                                        readOnly
                                    />
                                </div>
                            </div>
                        ))}
                    </div>
            </div>




            <div className="offcanvas offcanvas-end" tabIndex="-1" id="offcanvasRightEdit" aria-labelledby="offcanvasRightLabel" style={{ width: '40%' }}>
                <div className="offcanvas-header">
                    <h5 id="offcanvasRightLabel">Edit Request Pass</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"></button>
                </div>
                <div className="offcanvas-body">
                    <div className="row">
                        <div className="col-6 mb-2">
                            <label>Date</label>
                            <input type="date" name="Date" className="form-control" value={editFormData.Date} onChange={handleEditChange} />
                        </div>
                        <div className="col-6 mb-2">
                            <label>Meeting Date</label>
                            <input type="date" name="MeetingDate" className="form-control" value={editFormData.MeetingDate} onChange={handleEditChange} />
                        </div>
                        <div className="col-6 mb-2">
                            <label>Visitor Name</label>
                            <input type="text" name="VisitorName" className="form-control" value={editFormData.VisitorName} onChange={handleEditChange} />
                        </div>
                        <div className="col-6 mb-2">
                            <label>Vehicle Info</label>
                            <input type="text" name="VisitorName" className="form-control" value={editFormData.VehicleInfo} onChange={handleEditChange} />
                        </div>
                        {/* Additional fields... */}
                        
                        {/* Attendees */}
                        <h4 className="text-start my-2">Attendees</h4>
                        {editFormData && editFormData?.Attendees.map((attendee, index) => (
                            <div key={index} className="row mb-2">
                                <div className="col-4">
                                    <label>Name</label>
                                    <input
                                        type="text"
                                        className="form-control"
                                        value={attendee.AttendeName}
                                        onChange={(e) => handleEditAttendeeChange(index, 'AttendeName', e.target.value)}
                                    />
                                </div>
                                <div className="col-4">
                                    <label>Mobile</label>
                                    <input
                                        type="text"
                                        className="form-control"
                                        value={attendee.PhoneNumber}
                                        onChange={(e) => handleEditAttendeeChange(index, 'PhoneNumber', e.target.value)}
                                    />
                                </div>
                                <div className="col-4">
                                    <label>Email</label>
                                    <input
                                        type="text"
                                        className="form-control"
                                        value={attendee.Email}
                                        onChange={(e) => handleEditAttendeeChange(index, 'Email', e.target.value)}
                                    />
                                </div>
                                <button onClick={() => handleEditRemoveAttendee(index)}>Remove Attendee</button>
                            </div>
                        ))}
                        <button onClick={handleEditAddAttendee}>Add Attendee</button>
                    </div>
                    <button onClick={handleSave} className="btn btn-primary mt-3">Save Changes</button>
                </div>
            </div>

            {/* print */}
            <div className='d-none'>
                {printData ? (
                    <PrintPassContent ref={printRef} data={printData} qrCodeUrl={qrCodeUrl}/>
                ) : (
                    <p>Loading...</p>
                )}
            </div>
        </Base>
    )
}






