import React from 'react';
import './App.css';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { LoginComponent } from './login/login';
import VisitorComponent from './visitors/visitor';
import PassEmailComponent from './visitors/PassViewEmail';

function App() {
  return (
    <Router>
      <div>
        <Routes>
          <Route path="/visiting" element={<VisitorComponent />} />
          <Route path="/" element={<LoginComponent />} />
          <Route path="/email-pass/:RequestId" element={<PassEmailComponent />} />
        </Routes>
      </div>
    </Router>
  );
}

export default App;
