import React, { useState, useEffect } from "react";
import VMS_URL from "../config/config.jsx";
import axios from "axios";
import QRCode from "qrcode";
import Base from "../config/Base.jsx";
import { useParams } from 'react-router-dom';
import './passemail.css';

export default function PassEmailComponent() {

    const [sessionUserData, setsessionUserData] = useState([]);
    const [passData, setPassData] = useState([]);
    const { RequestId } = useParams();
    const [qrCodeUrl, setQrCodeUrl] = useState("");

    useEffect(() => {
        const userDataString = sessionStorage.getItem('userData');
        if (userDataString) {
            const userData = JSON.parse(userDataString);
            setsessionUserData(userData);
        } else {
            console.log('User data not found in sessionStorage');
        }
    }, []);

    const fetchViewEditData = async () => {
        console.log(RequestId)
        try {
            const response = await fetch(`${VMS_URL}getReqPassById?RequestId=${RequestId}`);
            if (response.ok) {
                const data = await response.json();
                if (data.Status) {
                    setPassData(data.ResultData);
                    console.log(data);
                } else {
                    setPassData([]);
                }
            } else {
                console.error('Failed to fetch attendees data:', response.statusText);
            }
        } catch (error) {
            console.error('Error fetching attendees data:', error.message);
        } finally {
            console.log('finally');
        }
    };

    useEffect(() => {
        fetchViewEditData();
    }, [RequestId]);

    useEffect(() => {
        if (passData && passData[0]?.AutoIncNo) {
            QRCode.toDataURL(passData[0].AutoIncNo.toString())
                .then((url) => {
                    setQrCodeUrl(url);
                })
                .catch((err) => {
                    console.error("Error generating QR code", err);
                    setQrCodeUrl("");
                });
        } else {
            console.error("AutoIncNo is missing");
            setQrCodeUrl("");
        }
    }, [passData]);

    

    return (
        <div className="container" style={{ margin: 'auto' }}>
            <div className="header">
                <h2>Visitor Pass</h2>
            </div>
            <div className="contact" style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start' }}>
                <div className="company-info">
                    <p>Organization:</p>
                    <p><i className="fa-solid fa-building" style={{ marginRight: '5px' }}></i>Copper Wind Industry</p>
                    <p><i className="fa-solid fa-phone" style={{ marginRight: '5px' }}></i>984xxxxxxx </p>
                    <p><i className="fa-solid fa-envelope" style={{ marginRight: '5px' }}></i>copperqind@gmail.com </p>
                </div>
                <div className="visitor-info" style={{ textAlign: 'right' }}>
                    <p>Visitor:</p>
                    <div className="info-row" style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end' }}>
                        <p> {passData[0]?.VisitorName} <i className="fa-solid fa-user"></i></p>
                        <p> {passData[0]?.Mobile} <i className="fa-solid fa-phone"></i></p>
                        <p> {passData[0]?.Email} <i className="fa-solid fa-envelope"></i></p>
                    </div>
                </div>
            </div>

            <div className="table-container">
                <table>
                    <tr>
                        <td>Visitor Id:</td>
                        <th>#{passData[0]?.AutoIncNo}</th>
                    </tr>
                    <tr>
                        <td>Visitor Date:</td>
                        <th>{passData[0] ? new Date(passData[0].Date).toLocaleDateString('en-GB') : 'N/A'}</th>
                    </tr>
                    {/* <tr>
                        <td>Entry Time:</td>
                        <th>13:04</th>
                    </tr>
                    <tr>
                        <td>Exit Time:</td>
                        <th>15:33</th>
                    </tr> */}
                    <tr>
                        <td>Vehicle No:</td>
                        <th>{passData[0]?.VehicleInfo}</th>
                    </tr>
                    <tr>
                        <td>No Of Visitors:</td>
                        <th>{passData?.length}</th>
                    </tr>
                    <tr>
                        <td>Notes:</td>
                        <th>{passData[0]?.Remarks}</th>
                    </tr>
                </table>
            </div>

            <div className="qr-code">
                {/* Conditionally render the QR code once it's generated */}
                {/* {qrCodeUrl ? ( */}
                    <img src={qrCodeUrl} alt="QR Code" width="160" height="160" />
                {/* ) : ( */}
                    {/* <p>Loading QR Code...</p> */}
                {/* )} */}
            </div>
        </div>
    );
}
